const HeroPicture = observer((props) => {
  let cls = "Rarity" + props.rarity;
  let cls2 = "RarityBorder" + props.rarity + " HeroPicture";
  let classImg = "img/classes/" + props.class + ".png";
  return(
    <div  className={cls2}>
    <img src={props.img} width="100%" alt="Picture of the hero"/>
    <div className={cls}>
      <div className="level2">
        {props.level} <br /> lvl
      </div>
      <div className="triangle-topleft2"></div>
      <div className="triangle-topright2"></div>
    </div>
    <img src={classImg} width="100%" className="ClassImg"/>
    </div>
  )
});

const Creature = observer((props) => {
  const [activeIndex, setActiveIndex] = useState(0);
  store.setActiveCreature(creatures[activeIndex].id);

  const next = () => {
    const nextIndex = activeIndex === creatures.length - 1 ? 0 : activeIndex + 1;
    setActiveIndex(nextIndex);
  }

  const previous = () => {
    const nextIndex = activeIndex === 0 ? creatures.length - 1 : activeIndex - 1;
    setActiveIndex(nextIndex);
  }


  const slides = creatures.map((item) => {
    return (
      <CarouselItem
        key={item.img}
      >
        <img src={item.img} width="100%" alt={item.name.en} />
        <div className="Rarity2">
          <div className="level2">
            {item.level * props.multiply} <br /> lvl
          </div>
          <div className="triangle-topleft2"></div>
          <div className="triangle-topright2"></div>
        </div>
        <div className="creature-exp">EXP: {item.exp * props.multiply}</div>
        <CarouselCaption captionText={item.description.en} captionHeader={item.name.en} />
        {/*
        <Row xs="8" className="CreatureStats">
          {item.stats.map((item, key) => <CreatureStat key = {key} id = {key} stat = {item} />)}
        </Row>
        */}
      </CarouselItem>
    );
  });
  return (
  <Carousel
    activeIndex={activeIndex}
    next={next}
    previous={previous}
    interval={false}
    className="Creatures"
  >
    {slides}
    <CarouselControl direction="prev" directionText="Previous" onClickHandler={previous} />
    <CarouselControl direction="next" directionText="Next" onClickHandler={next} />
  </Carousel>
  )
})

const Stat = observer(props =>{
  return (
    <div className="StatFlex">
      <div className="Stat" id={"param_desc" + props.id}>
      <img src={"img/stats/" + props.id + ".png"} width="100%" alt="St"/>
      <div className="StatText">{props.stat}</div>
      {props.stat < 100 ? <Button
        color="success"
        disabled={store.heroModalData.percentToNextLevel < 100 || store.heroModalData.order.creationTime != 0 || props.stat == 0}
        onClick={() => {
            store.levelUp(store.heroModalData.id, props.id);
            }}
        >
         &uarr;
      </Button>
      :
      <div className="StatMax">MAX</div>
      }
      </div>
      <UncontrolledTooltip placement="top" target={"param_desc" + props.id}>
        {messages.params_desc[props.id]}
      </UncontrolledTooltip>
    </div>
  )
})

const CreatureStat = props =>{
  return (
    <div className="StatFlex">
      <div className="CreatureStat" id={"param" + props.id}>
      <img src={"img/stats/" + props.id + ".png"} width="25px" alt="St"/>
      <div className="StatText">{props.stat}</div>
      </div>
      <UncontrolledTooltip placement="top" target={"param" + props.id}>
        {messages.params[props.id]}
      </UncontrolledTooltip>
    </div>
  )
}

const HeroClasses = observer(props => {
  return (
    <Modal isOpen={false}>
    <ModalHeader>Your Sponsor</ModalHeader>
    <ModalBody>
      <p  />
    </ModalBody>
    <ModalFooter>
      <Button color="success" >Ok</Button>
    </ModalFooter>
    </Modal>
  );
});

const FAQ = observer(() => {
  return (
    <Modal isOpen={store.uiState.FAQModal}>
    <ModalHeader toggle={()=>{store.closeFAQModal()}}>{messages.faq}</ModalHeader>
    <ModalBody>
    {faq.map((item, key) => <div key={key}><p className="question"><b>Q:</b> {item.q}</p><p className="answer"><b>A:</b> <span dangerouslySetInnerHTML={{__html: item.a}} /></p></div>)}
    </ModalBody>
    <ModalFooter>
      <Button color="success" onClick = {()=>{store.closeFAQModal()}}>Ok</Button>
    </ModalFooter>
    </Modal>
  );
});

const AppFooter = () => {
  return (        
    <footer>
      <div className="container">
      <Nav pills justified>
        {footerLinks.map((item, key) => <NavItem key={key}><NavLink href={item.url} target="_blank">{item.name}</NavLink></NavItem>)}
      </Nav>
      </div>
    </footer>
    );
}

export default App;
